.split-container {
      display: flex;
      height: 500px; /* Full height of the viewport */
      overflow: hidden; /* Prevents scrollbars from appearing when resizing */
  }

  .ExtractFullContent_left_panel {
      flex-grow: 1; /* Allows panels to grow and fill available space */
      overflow: auto; /* Enables scrolling within panels */
      align-items: center;
      text-align: center;
  }

  .ExtractFullContent_divider {
      cursor: ew-resize; /* Indicates the divider can be dragged horizontally */
      width: 10px; /* Width of the divider */
      background: #ccc; /* Color of the divider */
  }

.ExtractFullContent_title {
      margin-left: 30px;
      font-size: 20px;
      font-weight: bold;
}

.ExtractFullContent_right_panel {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding-bottom: 15px;
      margin: 10px;
}





.ExtractFullContent_upload_interface {
      display: flex;

      margin-left: 5%;
      margin-right: 5%;

      /* height: 40%; */


}

.ExtractFullContent_api_response {
      /* background-color: rgb(202, 101, 101); */
      margin-left: 5%;
      margin-right: 5%;
      text-align: left;
      white-space: pre-wrap;  /* Wraps the text and preserves whitespace */
      word-wrap: break-word;  /* Allows long words to be broken and wrapped to the next line */
      overflow-wrap: break-word; /* Ensures that if any single word is too long, it breaks and wraps to the next line */
  }
.ExtractFullContent_markdown {
      margin-left: 5%;
      margin-right: 5%;
      text-align: left;

      word-wrap: break-word;  /* Allows long words to be broken and wrapped to the next line */
}

.ExtractFullContent_loading_container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20%;
    flex-direction: column;
}

.ExtractFullContent_loading {
    display: inline-grid;
    width: 80px;
    aspect-ratio: 1;
    border-radius: 50%;
    overflow: hidden;
    outline: 7px solid #F24822;
    outline-offset: -1px;
    animation: l2-0 6s linear infinite;
    margin-bottom: 10vh;
}

.ExtractFullContent_loading:before,
.ExtractFullContent_loading:after {
    content:"";
    grid-area: 1/1;
}

.ExtractFullContent_loading:before {
    clip-path: polygon(evenodd,0 0,100% 0,100% 100%,0 100%,0 0,100% 50%,83.81% 59.06%,93.3% 75%,74.75% 74.75%,75% 93.3%,59.06% 83.81%,50% 100%,40.94% 83.81%,25% 93.3%,25.25% 74.75%,6.7% 75%,16.19% 59.06%,0% 50%,16.19% 40.94%,6.7% 25%,25.25% 25.25%,25% 6.7%,40.94% 16.19%,50% 0%,59.06% 16.19%,75% 6.7%,74.75% 25.25%,93.3% 25%,83.81% 40.94%,100% 50%);
    background: #F24822;
}

.ExtractFullContent_loading:after {
    margin: 12.5%;
    clip-path: polygon(100% 50%,78.19% 60.26%,88.3% 82.14%,65% 75.98%,58.68% 99.24%,44.79% 79.54%,25% 93.3%,27.02% 69.28%,3.02% 67.1%,20% 50%,3.02% 32.9%,27.02% 30.72%,25% 6.7%,44.79% 20.46%,58.68% 0.76%,65% 24.02%,88.3% 17.86%,78.19% 39.74%);
    background: #ff8000;
    transform: rotate(0turn) translate(15.625%) rotate(0turn);
    animation: l2-1 2s linear infinite;
}

@keyframes l2-0 {to{rotate: 1turn}}
@keyframes l2-1 {to{transform: rotate(1turn) translate(15.625%) rotate(-1.333turn)}}
