.DocumentParsing {

      min-width: 100vw;
      max-width: 100vw;
      background-color: #ffffff;
      max-height: 100%;
      min-height: 100%;
      overflow: hidden;

    }





.DocumentParsing_container {

      background-color: rgb(255, 255, 255);
      display: flex;

      min-height: 100vh;
      flex-wrap: nowrap;


}

.DocumentParsing_left_panel {

      width: 33%;
      min-width: 33%;
      max-width: 33%;
      height: 100%;
      font-family: Inter;




      margin-top: 20px;
      margin-bottom: 20px;

  background-color: #ffffff;

.DocumentParsing_left-header{
      font-size: 18px;
      font-weight: 700;
line-height: 21.78px;
 margin-left:2vw;
}

}
.DocumentParsing_right_panel {
      width: 67%;
      height: 100%;

      margin-top: 0px;
      margin-bottom: 0px;

      border-left : 4px solid #D9D9D9; /* This adds a split line between panels */

}
input[type="checkbox"] {
      accent-color: #F24822; /* Green color for the checkbox */

    }



.DocumentParsing_category_header {
      margin-left: 50px;
      margin-top: 30px;
      margin-bottom: 30px;


      font-family: Helvetica Neue;
      font-size: 18px;
      font-weight: 700;
      line-height: 21.98px;
      text-align: left;


}



.DocumentParsing_upload_interface {
      display: flex;

      margin-left: 5%;
      margin-right: 5%;

      /* height: 40%; */


}




.DocumentParsing_category-buttons {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
      padding-left: 50px;
      padding-right: 50px;
      /* margin-left: 20px;       */
      margin-top: 20px;

      gap: 20px;
      border-bottom: 2px solid #B3B3B3;
}

.DocumentParsing_category-buttons button {
      background: none;
      border: none;
      color: #000000; /* Default link color, change as needed */
      padding: 10px 15px;
      font-family: Inter;
      font-size: 16px;
      font-weight: 400;
      line-height: 19.36px;
      text-align: center;

      cursor: pointer;
      outline: none;
      text-decoration: none;
      border-bottom: 4px solid transparent; /* Invisible border by default */
      transition: color 0.3s ease, border-bottom 0.3s ease; /* Smooth transition for color and border */
}
.DocumentParsing_category-buttons button:hover,
.DocumentParsing_category-buttons button:focus {
      color: grey; /* Darker shade for hover state */
}

.DocumentParsing_category-buttons button.active {
      color:#F24822; /* Active state color */
      border-bottom: 4px solid#F24822; /* Underscore for active button */
}



@media (min-width: 300px) and (max-width: 767px) {
      .DocumentParsing_container {
            flex-direction: column;
      }
      .DocumentParsing_left_panel{
            width: 100%;
            min-width: 100%;
            height: 30vh;
            min-height: 45vh;
            max-height: 45vh;
      }
      .DocumentParsing_right_panel {
            width: 100%;
            min-width: 100%;
            padding-bottom: 3vh;
            min-height: 150vh;
            max-height: 200vh;
            border: none;
            border-top: 4px solid #D9D9D9 ;

      }

}